$login-primary: #FAFAFA;
$login-secondary: #878787;


body.login {
    background-color: $login-primary;

    #nav,
    #backtoblog {
        a {
            color: $login-secondary;
            &:hover{
                color: $login-secondary;
                text-decoration: underline;
            }
        }
    }

    #login {
        padding: 5%;

        h1 {
            display: none;
        }

        svg{
            display: block;
            width: 50px;
            margin: 2em auto;
        }

        form {
            border: 3px solid $login-primary;

            input:focus {
                border: 1px solid $login-primary;
            }
        }

        .button-primary {
            border: 1px solid $login-primary;
            background: $login-secondary;
            color: $login-primary;
            text-shadow: none;
            box-shadow: none;

            &:hover {
                background: darken($login-secondary, 10%);
                color: $login-primary;
            }
        }
    }

    .creator {
        max-width: 300px;
        margin: 120px auto 20px;
        padding: 0 24px;
        color: $login-secondary;;

        small {
            display: block;
            margin-bottom: 15px;
        }

        .creator-logo {
            display: block;
            fill-rule: evenodd;
            clip-rule: evenodd;
            fill: $login-secondary;
        }
    }
}